  // dialog 
function scrollTopByClassName(className: string){
  const els = document.getElementsByClassName(className);
  if(els && els.length){
    els[0].scrollTop = 0;
  }
}

export function scrollTopDialog(){
  scrollTopByClassName("v-dialog--active");
}

export function scrollRightDialog(){
  const els = document.getElementsByClassName("vertical-sentence");
  if(els && els.length){
    els[0].scrollLeft = 0;
  }
}
export function scrollTopMain(){
  scrollTopByClassName("v-content");
}